.pull-history-tooltip {
  position: absolute;
  top: 100%;
  right: -10vw;
  margin-top: 32px;
  z-index: 1000;
  width: 80vw
}

.pull-history-container {
  background: rgba(2, 6, 23, 0.95);
  border: 1px solid rgba(148, 163, 184, 0.3);
  border-radius: 16px;
  padding: 8px 0;
  display: flex;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(20px);
}

.pull-history-stats {
  width: 160px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
}

.pull-history-stats__left {
  display: flex;
  flex-direction: column;
  border-right: 1px solid rgba(148, 163, 184, 0.3);
  width: 80px;
  justify-content: space-between;
}

.pull-history-stats__right {
  display: flex;
  flex-direction: column;
  border-right: 1px solid rgba(148, 163, 184, 0.3);
  width: 80px;
  justify-content: space-between;
}

.stat-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
  text-align: center;
}

.stat-item__label {
  font-size: 0.85rem;
  color: #94a3b8;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 600;
}

.stat-item__value {
  font-size: 1.4rem;
  font-weight: 700;
  color: #e2e8f0;
  line-height: 1;
}

.stat-item__rate {
  font-size: 0.9rem;
  color: #cbd5e1;
}

.pull-history-timeline {
  flex: 1;
  min-width: 0;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 20px 0;
}

.pull-history-timeline::-webkit-scrollbar {
  height: 8px;
}

.pull-history-timeline::-webkit-scrollbar-track {
  background: rgba(15, 23, 42, 0.3);
  border-radius: 4px;
}

.pull-history-timeline::-webkit-scrollbar-thumb {
  background: rgba(148, 163, 184, 0.3);
  border-radius: 4px;
}

.pull-history-timeline::-webkit-scrollbar-thumb:hover {
  background: rgba(148, 163, 184, 0.5);
}

.timeline-wrapper {
  position: relative;
  height: 120px;
}

.timeline-line {
  position: relative;
  width: 100%;
  height: 4px;
  background: rgba(148, 163, 184, 0.3);
  top: 50%;
  transform: translateY(-50%);
  border-radius: 2px;
}

.timeline-mark {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

.timeline-mark__line {
  width: 2px;
  height: 16px;
  background: rgba(148, 163, 184, 0.5);
  border-radius: 1px;
}

.timeline-mark__label {
  font-size: 0.75rem;
  color: #94a3b8;
  font-weight: 600;
  white-space: nowrap;
}

.timeline-dot {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  z-index: 10;
}

.timeline-dot:hover {
  transform: translate(-50%, -50%) scale(1.3);
  box-shadow: 0 0 12px currentColor;
}

.timeline-dot--five {
  background: linear-gradient(135deg, #fbbf24, #f59e0b);
  border: 2px solid rgba(251, 191, 36, 0.6);
  box-shadow: 0 0 8px rgba(251, 191, 36, 0.4);
}

.timeline-dot--six {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  border: 2px solid rgba(239, 68, 68, 0.6);
  box-shadow: 0 0 8px rgba(239, 68, 68, 0.4);
}

.timeline-dot__label {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.75rem;
  font-weight: 600;
  color: #e2e8f0;
  background: rgba(2, 6, 23, 0.95);
  padding: 4px 8px;
  border-radius: 6px;
  border: 1px solid rgba(148, 163, 184, 0.3);
  white-space: nowrap;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.timeline-dot__label--above {
  bottom: 100%;
  margin-bottom: 32px;
}

.timeline-dot__label--below {
  top: 100%;
  margin-top: 32px;
}

.timeline-dot__label--hover {
  bottom: 100%;
  margin-bottom: 8px;
}

.timeline-dot.is-win .timeline-dot__label--above,
.timeline-dot.is-lose .timeline-dot__label--below {
  opacity: 1;
}

.timeline-dot--five:hover .timeline-dot__label--hover {
  opacity: 1;
}

.pity-label {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.pity-label--above {
  bottom: 100%;
  margin-bottom: 36px;
}

.pity-label--below {
  top: 100%;
  margin-top: 36px;
}

.pity-label__line {
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
}

.pity-label__text {
  position: relative;
  padding: 3px 6px;
  border-radius: 999px;
  font-size: 0.7rem;
  font-weight: 600;
  white-space: nowrap;
  color: #e2e8f0;
  background: rgba(15, 23, 42, 0.95);
  border: 1px solid rgba(148, 163, 184, 0.4);
}

.pity-label--green .pity-label__text {
  color: #bbf7d0;
  border-color: rgba(34, 197, 94, 0.5);
}

.pity-label__line.pity-label--green {
  background: rgba(34, 197, 94, 0.4);
}

.pity-label--blue .pity-label__text {
  color: #bfdbfe;
  border-color: rgba(59, 130, 246, 0.5);
}

.pity-label__line.pity-label--blue {
  background: rgba(59, 130, 246, 0.4);
}

.pity-label--yellow .pity-label__text {
  color: #fef9c3;
  border-color: rgba(234, 179, 8, 0.7);
}

.pity-label__line.pity-label--yellow {
  background: rgba(234, 179, 8, 0.5);
}

.pity-label--red .pity-label__text {
  color: #fecaca;
  border-color: rgba(248, 113, 113, 0.7);
}

.pity-label__line.pity-label--red {
  background: rgba(248, 113, 113, 0.5);
}

.pity-label--purple .pity-label__text {
  color: #e9d5ff;
  border-color: rgba(168, 85, 247, 0.7);
}

.pity-label__line.pity-label--purple {
  background: rgba(168, 85, 247, 0.5);
}

@media (max-width: 1200px) {
  .pull-history-tooltip {
    min-width: 700px;
  }

  .pull-history-stats {
    width: 200px;
  }
}

@media (max-width: 768px) {
  .pull-history-container {
    flex-direction: column;
  }

  .pull-history-stats {
    width: 100%;
    flex-direction: row;
  }

  .pull-history-stats__left,
  .pull-history-stats__right {
    flex: 1;
  }

  .pull-history-tooltip {
    min-width: auto;
    width: 100%;
  }
}

